.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 2rem;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1000;
  background-color: rgba(0, 0, 0, 0.8);
  backdrop-filter: blur(5px);
  transition: background-color 0.3s ease;
}

.logo img {
  margin-top: 5px;
  height: 2rem;
}

nav {
  display: flex;
  align-items: center;
  margin-left: auto;
  max-width: 1200px;
}

nav a,
.toggleContainer {
  color: #ececec;
  text-decoration: none;
  font-weight: bold;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 40px;
  padding: 0 1rem;
  border-radius: 20px;
  transition: all 0.3s ease;
  min-width: 4rem;
}

nav a:hover {
  color: #00aeff;
}

nav a::after {
  content: "";
  position: absolute;
  left: 50%;
  bottom: -3px;
  width: 0;
  height: 2px;
  background-color: #00aeff;
  transition: width 0.3s ease, left 0.3s ease;
}

nav a:hover::after {
  width: 100%;
  left: 0;
}

nav a.active {
  color: #00aeff;
}

.toggleContainer {
  width: 6rem;
  padding: 0 0.5rem;
}

.toggleContainer:hover .langText {
  color: #00aeff;
}

.toggle {
  position: relative;
  width: 4.5rem;
  height: 2rem;
  background: #292929;
  border-radius: 20px;
  margin: 0;
  padding: 0;
  transition: background 0.3sease-in-out;
  display: flex;
  align-items: center;
  cursor: pointer;
  justify-content: space-between;
}

.toggleSwitch {
  position: absolute;
  width: 1rem;
  height: 1rem;
  background: white;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: transform 0.3s ease-in-out;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
}

.english {
  transform: translateX(0.5rem); //left: 0
}

.korean {
  transform: translateX(3rem); //right: 3.5rem
}

.langText {
  position: absolute;
  font-size: 14px;
  color: #ececec;
  font-weight: bold;
  transition: transform 0.3s ease-in-out, color 0.3s ease-in-out;
}

.langLeft {
  transform: translateX(0.5rem);
}

.langRight {
  transform: translateX(2.75rem);
}

.flag img {
  width: 18px;
  height: 18px;
  object-fit: contain;
  display: block;
}

.menuIcon {
  display: none;
  cursor: pointer;
  font-size: 24px;
  color: white;
}

@media (max-width: 768px) {
  .header {
    padding: 0.75rem 1.5rem;
  }

  .logo img {
    height: 30px;
  }

  nav {
    flex-direction: column;
    position: absolute;
    top: 60px;
    right: 0;
    background: rgba(0, 0, 0, 0.9);
    width: 100%;
    height: 0;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 2rem;
    padding: 0;
    transition: height 0.3s ease-in-out, padding 0.3s ease-in-out;
  }

  nav.open {
    height: 250px;
    padding: 1rem;
  }

  nav a {
    width: 100%;
    min-width: 80px;
    text-align: center;
    display: inline-block;
    padding: 1rem 0;
    opacity: 0;
    transform: translateY(-20px);
    transition: opacity 0.3s ease-in-out, transform 0.3s ease-in-out;
  }

  nav.open a {
    opacity: 1;
    transform: translateY(0);
  }

  .toggleContainer {
    width: 3.5rem;
    height: 1.25rem;
  }

  .toggleSwitch {
    width: 0.9rem;
    height: 0.9rem;
  }

  .english {
    transform: translateX(0.1rem);
  }

  .korean {
    transform: translateX(2.6rem);
  }

  .langLeft {
    transform: translateX(0.3rem);
  }

  .langRight {
    transform: translateX(2rem);
  }

  .menuIcon {
    display: block;
  }
}

#content {
  padding-top: 10px;
}
