.bjsCanvas {
  width: 100vw;
  height: 100dvh;
  min-width: 280px;
  min-height: 200px;
  outline: none !important;
  border: none !important;
  -webkit-tap-highlight-color: transparent;
  touch-action: manipulation;
  user-select: none;
}

.bjsCanvas:focus,
.bjsCanvas:focus-visible {
  outline: none !important;
  border: none !important;
  user-select: none;
  -webkit-tap-highlight-color: transparent;
  pointer-events: auto !important;
}

.bjsCanvas::-moz-focus-inner {
  border: 0;
}

@media (max-width: 768px) {
  .bjsCanvas {
    width: 100vw;
    height: 75vh;
  }
}

@media (orientation: landscape) {
  .bjsCanvas {
    min-height: 100dvw;
  }
}
