.layout {
  display: flex;
  flex-direction: column;
  margin-top: 3rem;
  min-height: 100vh;
  min-height: 100vh;
  font-family: "GmarketSansMedium";
  line-height: 1.5;
  background-color: #d8dee9 !important;
  color: var(--color-text-base);
  transition: background-color 0.3s, color 0.3s;
  cursor: default;
  scroll-behavior: smooth;
}

.ideaMain {
  display: flex;
  flex: 1;
  padding-left: 0;
  margin: 2rem 0;
}

.ideaLabContent {
  flex: 1;
  margin: 0 auto;
}

.title {
  font-family: "Giants";
  font-size: 2rem;
  margin-bottom: 2rem;
}

@media (max-width: 768px) {
  .main {
    flex-direction: column;
  }
}
.fixedHeader {
  position: fixed;
  top: 4.5rem;
  width: 100%;
  display: flex;
  flex-direction: column;

  background-color: #d8dee9;
  padding: 1rem 2rem;
  z-index: 10;
  flex-wrap: wrap;
  box-sizing: border-box;
}

.title {
  font-family: "Giants";
  font-size: 2rem;
  margin: 1rem 0;
}

@media (max-width: 768px) {
  .fixedHeader {
    top: 4rem;
    padding: 0.5rem 2.5rem;
    max-width: 100%;
    flex-direction: column;
  }

  .fixedHeader > * {
    width: 100%;
    text-align: center;
  }
}
