.layout {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  width: 100%;
  font-family: "GmarketSansMedium";
  line-height: 1.5;
  background-color: #d8dee9 !important;
  color: var(--color-text-base);
  transition: background-color 0.3s, color 0.3s;
  // cursor: default;
  overflow-x: hidden;
}

.blogMain {
  display: flex;
  flex: 1;
  padding: 2rem 1rem;
  margin-top: 5rem;
  position: relative;
  width: 100%;
  max-width: 90vw;
  margin-left: 1rem;
  margin-right: 2rem;
  justify-content: center;
  align-items: flex-start;
  gap: 2rem;

  @media (max-width: 1024px) {
    max-width: 95vw;
    justify-content: flex-start;
  }

  @media (max-width: 768px) {
    max-width: 90vw;
    justify-content: flex-start;
  }
}

.blogContent {
  flex: 1;
  max-width: 800px;
  width: 100%;
  min-width: 300px;
  display: flex;
  justify-content: center;

  @media (max-width: 1024px) {
    max-width: calc(100% - 2rem);
    min-width: calc(100% - 3rem);
  }
}

.sidebarContainer {
  flex-basis: 300px;
  flex-shrink: 0;
  display: flex;
  justify-content: center;

  @media (max-width: 1024px) {
    flex-basis: auto;
    max-width: 100%;
    width: 100%;
    justify-content: center;
  }

  @media (max-width: 768px) {
    max-width: 100%;
    width: auto;
    margin: 1rem 0;
    justify-content: center;
  }
}

.backButton {
  position: fixed;
  top: 50%;
  left: 0;
  width: 60px;
  height: 60px;
  background: linear-gradient(45deg, rgba(0, 174, 255, 0.5), rgba(162, 0, 255, 0.5));
  color: white;
  font-size: 2rem;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  transform: scale(0);
  transition: transform 0.2s ease, left 0.2s ease;
  clip-path: circle(50%);
  z-index: 999;
}

.show {
  transform: scale(1);
}

@supports (filter: url("#goo")) {
  .backButton {
    filter: url("#goo");
  }
}
