@import "./styles/_fonts.scss";

body {
  font-family: "Giants";
  color: #333;
  background-color: #121212;
  min-height: 100vh;
  margin: 0;
  padding: 0;
  transition: background-color 0.3s ease, color 0.3s ease;

  user-select: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
}

.app {
  padding-top: 60px;
  font-family: "Giants";
}

.mouseScroll {
  position: fixed;
  width: 4rem;
  bottom: 10px;
  right: 0;
  left: 0;
  margin: 0 auto;
  text-align: center;
  color: #ececec;
  font-size: 10px;
  text-transform: uppercase;
  font-weight: 500;
}
.mouse {
  width: 26px;
  height: 40px;
  border-radius: 15px;
  border: 2px solid #ececec;
  overflow: hidden;
  position: relative;
  margin: 0 auto 10px;
  color: #ececec;
}
.mouse .roll {
  position: absolute;
  top: 8px;
  left: 50%;
  width: 4px;
  margin-left: -2px;
  height: 4px;
  border-radius: 4px;
  background: var(--progress-color);
  -webkit-animation: mouseScroll 1s cubic-bezier(0.7, 0, 0.3, 1) infinite alternate;
  animation: mouseScroll 1s cubic-bezier(0.7, 0, 0.3, 1) infinite alternate;
  z-index: 2;
}
.mouse .rollshadow {
  position: absolute;
  top: 8px;
  left: 50%;
  width: 4px;
  margin-left: -2px;
  height: 4px;
  background: #ececec;
  border-radius: 4px;
  -webkit-animation: mouseScroll 1s cubic-bezier(0.7, 0, 0.3, 1) infinite alternate;
  animation: mouseScroll 1s cubic-bezier(0.7, 0, 0.3, 1) infinite alternate;
  animation-delay: 0s;
  -webkit-animation-delay: 80ms;
  animation-delay: 80ms;
  z-index: 1;
}

@-webkit-keyframes mouseScroll {
  0% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }
  100% {
    -webkit-transform: translateY(14px);
    transform: translateY(14px);
  }
}

@keyframes mouseScroll {
  0% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }
  100% {
    -webkit-transform: translateY(14px);
    transform: translateY(14px);
  }
}
