.blogPost {
  h1 {
    font-size: 2.5rem;
    margin-bottom: 0.5rem;
  }

  h2 {
    font-size: 2rem;
    margin-top: 2rem;
    margin-bottom: 1rem;
  }

  h3 {
    font-size: 1.5rem;
    margin-top: 1.5rem;
    margin-bottom: 0.75rem;
  }
}

.meta {
  color: #666;
  font-size: 0.9rem;
  margin-bottom: 2rem;
}

.content {
  line-height: 1.6;

  p {
    margin-bottom: 1rem;
  }

  ul,
  ol {
    margin-bottom: 1rem;
    padding-left: 2rem;
  }

  img {
    max-width: 100%;
    height: auto;
    margin: 1rem 0;
  }

  pre {
    margin-bottom: 1rem;
    padding: 1rem;
    overflow-x: auto;
    background-color: #f4f4f4;
    border-radius: 4px;
  }

  code {
    font-family: "Courier New", Courier, monospace;
    font-size: 0.9em;
    padding: 0.2em 0.4em;
    background-color: #1f1f1f;
    border-radius: 3px;
  }

  blockquote {
    margin: 0 0 1rem;
    padding: 0.5rem 1rem;
    border-left: 4px solid #ccc;
    background-color: #f9f9f9;
    font-style: italic;
  }
}
