.container {
  background-color: #222;
  color: #ececec;
  text-align: center;
  padding: 1rem;
  transition: background-color 0.3s ease, color 0.3s ease;
  font-family: Roboto;
  display: flex;
  align-items: center;
  justify-content: center;
}

.footer {
  width: 60%;
  display: flex;
  flex-direction: column;
  align-items: center;
  z-index: 1001;
}

.icons {
  display: flex;
  gap: 1.5rem;
  margin-bottom: 0.5rem;
  padding: 0 2rem;
}

.icon {
  font-size: 1.5rem;
  cursor: pointer;
  transition: color 0.3s ease;
}

.icon:hover {
  color: #00aeff;
}

.border {
  border: none;
  height: 1px;
  width: 100%;
  background-color: rgb(73, 73, 73);
}

.footer .icons {
  pointer-events: auto !important;
}

.footer:hover ~ .cursor {
  opacity: 0;
  visibility: hidden;
}
