.cursor {
  pointer-events: none;
  position: fixed;
  display: block;
  top: 0;
  left: 0;
  z-index: 1000;
  filter: url("#goo");

  span {
    position: absolute;
    display: block;
    width: 20px;
    height: 20px;
    border-radius: 20px;
    background: linear-gradient(45deg, #00aeff, #a200ff);
    transform-origin: center center;
    transform: translate(-50%, -50%);
  }
}
.cursor.hovered span {
  background: linear-gradient(45deg, #00aeff44, #a200ff44);
  mix-blend-mode: difference;
}
