.hero {
  display: flex;
  flex-direction: row;
  height: 100vh;
  background-color: #1a1a1a;
  overflow: hidden;
}

.hero > :first-child {
  flex: 0 0 60%;
  min-width: 320px;
  min-height: 648px;
}

.hero .content {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 2rem;
  text-align: center;
  z-index: 1;
}

.hero h1 {
  font-size: 4rem;
  margin-bottom: 1rem;
  background: linear-gradient(45deg, #00aeff, #a200ff);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-family: "Giants";
  text-align: center;
  white-space: pre-line;
  line-height: 1.2;
  word-break: keep-all;
}

.hero p {
  font-size: 1.25rem;
  margin-bottom: 2rem;
  color: #ececec;
  word-break: keep-all;
  white-space: normal;
  font-family: "GmarketSansMedium";
  text-align: center;
  max-width: 70%;
  line-height: 1.6;
  word-spacing: 0.1rem;
  letter-spacing: 0.03rem;
  text-wrap: balance;
  text-align: center;
  white-space: pre-line;
  word-break: keep-all;
}

.hero .blogBtn {
  padding: 1rem 2rem;
  font-size: 1.2rem;
  background: linear-gradient(45deg, #00aeff, #a200ff);
  border: none;
  border-radius: 50px;
  color: #ececec;
  cursor: pointer;
  transition: transform 0.3s ease;
  font-family: "Giants";
  position: relative;
  overflow: hidden;
  transition: transform 0.3s ease, box-shadow 0.3s ease, background 0.5s ease-in-out;
  margin-top: 7rem;
}

.hero .blogBtn::before {
  content: "";
  position: absolute;
  top: 0;
  left: -100%;
  width: 300%;
  height: 100%;
  background: linear-gradient(45deg, #00aeff, #a200ff, #00aeff);
  transition: left 0.5s ease-in-out;
  z-index: -1;
}

.hero .blogBtn:hover::before {
  left: 0;
}

.hero .blogBtn::after {
  content: "";
  position: absolute;
  top: 50%;
  left: -50%;
  width: 120%;
  height: 200%;
  background: rgba(255, 255, 255, 0.3);
  transform: rotate(30deg);
  transition: left 0.5s ease-out;
}

.hero .blogBtn:hover::after {
  left: 150%;
}

.hero .blogBtn:hover {
  transform: scale(1.07) rotate(1deg);
  box-shadow: 0 10px 20px rgba(0, 174, 255, 0.4);
}

@media (max-width: 1124px) {
  .hero {
    flex-direction: column;
    height: auto;
  }
  .hero > :first-child {
    flex: none;
  }
  .hero h1 {
    font-size: 3rem;
  }
  .hero p {
    font-size: 1.2rem;
  }

  .hero .blogBtn {
    margin-top: 0;
  }
}

@media (max-width: 1024px) {
  .hero h1 {
    font-size: 3.5rem;
    max-width: 80%;
    line-height: 1.3;
  }
  .hero .blogBtn {
    margin-top: 0;
  }
}

@media (max-width: 768px) {
  .hero h1 {
    font-size: 3rem;
    max-width: 85%;
    line-height: 1.4;
  }
  .hero .blogBtn {
    margin-top: 0;
    margin-bottom: 2rem;
  }
}

@media (max-width: 480px) {
  .hero h1 {
    font-size: 2.5rem;
    max-width: 90%;
    line-height: 1.5;
  }
  .hero .blogBtn {
    margin-top: 0;
  }
}
