.blogList {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
  align-items: center;
  width: 100%;
}

.blogPost {
  width: 100%;
  background: #d8dee9;
  margin-bottom: 1rem;
  border-radius: 12px;
  box-shadow: 8px 8px 16px rgba(30, 50, 80, 0.2), -8px -8px 16px rgba(255, 255, 255, 0.7);
  transition: all 0.2s ease-in-out;
  overflow: hidden;
}

.postContent {
  display: flex;
  flex-direction: column;
  padding: 1.5rem;
}

.contentRow {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  gap: 1.5rem;
}

.textContent {
  display: flex;
  flex-direction: column;
  flex: 1;
}

.thumbnail {
  flex-shrink: 0;
  width: 12rem;
  height: 12rem;
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 4px 4px 8px rgba(30, 50, 80, 0.2);
}

.thumbnail img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

@media (max-width: 768px) {
  .contentRow {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: 100%;
  }

  .thumbnail {
    width: 100%;
    max-width: 18rem;
    max-height: 16rem;
    margin-bottom: 1rem;
  }
}

.blogPost h2 {
  font-size: 1.3rem;
  color: #2e3440;
  margin: 0;
  margin-bottom: 0.5rem;
}

.blogPost h2 a:hover {
  color: #551bd3;
  text-decoration: underline;
  transform: scale(1.05);
}

.blogPost .meta {
  font-size: 0.9rem;
  color: #4c566a;
  margin: 0.5rem 0;
}

.blogPost p {
  font-size: 1rem;
  color: #2e3440;
  margin: 0.5rem 0;
}

.pagination {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 0.8rem;
  margin-top: 1.5rem;
}

.pagination button {
  width: 2.8rem;
  height: 2.8rem;
  background: #6359e9;
  color: white;
  border: none;
  border-radius: 8px;
  font-size: 1.5rem;
  line-height: 1;
  font-family: "GmarketSansMedium";
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: background 0.2s ease-in-out;
}

.pagination button:hover {
  background: #5148c6;
}

.pagination button:disabled {
  background: #a0a0a0;
  cursor: not-allowed;
}

.pagination span {
  font-size: 1.1rem;
  min-width: 60px;
  text-align: center;
}

a {
  text-decoration: none;
}

@media screen and (max-width: 600px) {
  .pagination {
    flex-wrap: wrap;
    justify-content: center;
    gap: 0.5rem;
  }

  .pagination button {
    width: 2.4rem;
    height: 2.4rem;
    font-size: 1.3rem;
  }

  .pagination span {
    font-size: 1rem;
    min-width: 50px;
  }
}
