.gallery {
  width: 100%;
  display: flex;
  justify-content: center;
  font-family: "GmarketSansMedium";
  margin-top: 9rem;
}

.galleryTrack {
  position: fixed;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 0.25rem;
  padding: 0.25rem;
  will-change: transform;
  margin: 0 2rem;
  cursor: none;
}

.card {
  height: 25rem;
  border-radius: 5px;
  overflow: hidden;

  & .cardImageWrapper {
    height: 135%;
    will-change: transform;

    & img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
}

@media (width < 800px) {
  .galleryTrack {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (width < 550px) {
  .galleryTrack {
    grid-template-columns: repeat(1, 1fr);
  }
}

.card {
  position: relative;
  overflow: hidden;
  border-radius: 12px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
}

.cardImageWrapper {
  position: relative;
  overflow: hidden;
}

.cardImage {
  width: 100%;
  height: auto;
  transition: transform 0.3s ease-in-out;
}

.card:hover .cardImage {
  transform: scale(1.1);
}

.cardOverlay {
  position: absolute;
  inset: 0;
  background: rgba(0, 0, 0, 0.8);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  color: white;
  opacity: 0;
  transition: opacity 0.3s ease-in-out;
  padding: 16px;
}

.card:hover .cardOverlay {
  opacity: 1;
}

.cardTitle {
  font-family: "Giants";
  font-size: 1.5rem;
  font-weight: bold;
  text-shadow: 1px 2px 2px rgb(255, 0, 0);
}

.cardCategory {
  font-size: 0.875rem;
  opacity: 0.8;
}

.cardButton {
  margin-top: 12px;
  padding: 8px 16px;
  background: #ffcc00;
  color: black;
  font-weight: bold;
  border-radius: 6px;
  transition: background 0.3s ease-in-out;
  text-decoration: none;
}

.cardButton {
  all: unset;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 5rem;
  padding: 0.5rem 1rem;
  background: #ffcc00;
  color: black;
  font-weight: bold;
  font-size: 1rem;
  text-decoration: none;
  border-radius: 8px;
  cursor: pointer;
  transition: background 0.2s ease-in-out;
  box-sizing: border-box;
  text-align: center;
}

.cardButton:hover {
  background: #e6b800;
}

.categoryContainer {
  display: flex;
  gap: 10px;
}

.categoryTag {
  padding: 6px 12px;
  border-radius: 20px;
  background-color: #f0f0f0;
  cursor: pointer;
  transition: 0.3s;
  min-width: 80px;
  text-align: center;
  border: none;
}

.categoryTag:hover {
  background-color: #ddd;
}

.active {
  background-color: #ffcc00;
  font-weight: bold;
}
