.container {
  justify-content: center;
  align-items: center;
  overflow: hidden;
  text-align: center;
  padding: 2rem;
  background: linear-gradient(45deg, #1a1a1a, #2a2a2a);
  transition: background 0.3s ease;
  transform-style: preserve-3d;
  // min-width: 100%;
  max-width: 100vw;
  margin: 0 auto;
}

.sectionTitle {
  font-size: 2.5rem;
  font-weight: 800;
  letter-spacing: -1px;
  margin: 0 auto 2rem;
  text-align: center;
  width: 100%;
  background: linear-gradient(45deg, #00aeff, #a200ff);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  transition: transform 0.1s linear;
  max-width: 90vw;

  display: block;
}

.cardsWrapper {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 2rem;
  justify-content: center;
  align-items: center;
  margin-bottom: 30px;
  width: 100%;
  max-width: 90vw;
  margin-left: auto;
  margin-right: auto;
}

.cardContainer {
  display: flex;
  justify-content: center;
  flex-grow: 1;
  min-width: 18rem;
  max-width: 90vw;
  perspective: 1200px;
}

.card {
  width: 100%;
  height: 15rem;
  position: relative;
  border-radius: 25px;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  transform-style: preserve-3d;
  box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.17);
  cursor: pointer;
}

.card:hover {
  transform: rotateX(5deg) rotateY(5deg) scale(1.05);
  box-shadow: 0px 10px 30px rgba(255, 255, 255, 0.2);
}

.rotated {
  transform: rotateY(-180deg);
}

.rotated:hover {
  transform: rotateY(-180deg) scale(1.05);
  box-shadow: 0px 10px 30px rgba(255, 255, 255, 0.2);
}

.cardContents {
  width: 100%;
  height: 100%;
  border-radius: 25px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-align: center;
  position: absolute;
  top: 0;
  left: 0;
  backface-visibility: hidden;

  h2,
  p {
    text-shadow: 1px 2px 2px rgba(0, 0, 0, 0.2);
  }

  h2 {
    font-size: 1.5rem;
    font-weight: 700;
    padding: 0 10px;
    color: #ececec;
    max-width: 100%;
    text-align: center;
    line-height: 1.4;
    white-space: pre-line;
  }

  hr {
    width: 50%;
    margin: 20px auto;
  }

  p {
    font-size: 1rem;
    font-weight: 700;
    padding: 0 10px;
    color: rgba(255, 255, 255, 0.5);
  }
}

.cardDepth {
  transform: translateZ(100px) scale(0.98);
  perspective: inherit;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 90%;
}

.cardFront {
  background: linear-gradient(to top left, #5b6d9d, #0f0f2d);
  transform-style: preserve-3d;
}
.cardFront::before {
  content: "";
  position: absolute;
  top: 7px;
  left: 7px;
  right: 7px;
  bottom: 7px;
  border: 1.5px solid rgba(255, 255, 255, 0.5);
  border-radius: 25px;
  pointer-events: none;
}

.cardBack {
  transform: rotateY(180deg);
  background: linear-gradient(to top left, #b9755c, #3370a3);
  transform-style: preserve-3d;
}

/* Responsive */
@media (max-width: 1024px) {
  .sectionTitle {
    font-size: 2rem;
  }

  .cardsWrapper {
    grid-template-columns: repeat(2, minmax(280px, 1fr));
    gap: 1.5rem;
  }

  .card {
    height: 13rem;
  }

  .cardContents h2 {
    font-size: 1.3rem;
  }

  .cardContents p {
    font-size: 0.95rem;
  }
}

@media (max-width: 768px) {
  .sectionTitle {
    font-size: 1.8rem;
    margin-bottom: 1.5rem;
  }

  .cardsWrapper {
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 1.5rem;
  }

  .card {
    height: 12rem;
  }

  .cardContents h2 {
    font-size: 1.2rem;
  }

  .cardContents p {
    font-size: 0.9rem;
  }
}

@media (max-width: 480px) {
  .sectionTitle {
    font-size: 1.6rem;
    margin-bottom: 1rem;
  }

  .cardsWrapper {
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    gap: 1rem;
  }
  .container {
    padding: 1.5rem;
  }

  .card {
    height: 11rem;
  }

  .cardContents h2 {
    font-size: 1.1rem;
  }

  .cardContents p {
    font-size: 0.85rem;
  }
}
