.sidebar {
  width: 300px;
  height: auto;
  max-height: calc(100vh - 6rem);
  overflow-y: auto;
  background: #d8dee9;
  border-radius: 12px;
  box-shadow: 8px 8px 16px rgba(30, 50, 80, 0.2), -8px -8px 16px rgba(255, 255, 255, 0.7);
  transition: all 0.3s ease-in-out;
  position: relative;
  top: auto;
  left: auto;

  @media (max-width: 1024px) {
    position: fixed;
    top: 8rem;
    left: 2rem;
    width: 80%;
    max-width: 280px;
    height: auto;
    max-height: 90vh;
    z-index: 200;
    transform: translateX(-120%);
    transition: transform 0.3s ease-in-out, opacity 0.3s ease-in-out;
    opacity: 0;
  }
}

.sidebar.open {
  transform: translateX(0);
  opacity: 1;
}

/* Sidebar Header */
h2 {
  font-size: 1.5rem;
}

/* Mobile Sidebar Toggle Button */
.menuButton {
  display: none;

  @media (max-width: 1024px) {
    display: block;
    position: fixed;
    top: 5rem;
    left: 2rem;
    background: #6359e9;
    color: white;
    border: none;
    padding: 0.5rem 1rem;
    border-radius: 6px;
    font-size: 1rem;
    cursor: pointer;
    z-index: 250;
    opacity: 0;
    transform: translateY(-50%);
    transition: opacity 0.3s ease, transform 0.3s ease;
  }
}

.menuButton.visible {
  opacity: 1;
  transform: translateY(0);
}

.menuButton.hidden {
  opacity: 0;
  transform: translateY(-50%);
}

/* Close Button */
.closeButton {
  display: none;

  @media (max-width: 1024px) {
    display: block;
    background: none;
    border: none;
    font-size: 1.5rem;
    position: absolute;
    top: 10px;
    right: 15px;
    cursor: pointer;
    color: #2e3440;
  }
}

/* Sidebar Widgets */
.widget {
  background-color: #d8dee9;
  padding: 1rem 1.5rem;
  border-radius: 12px;
  box-shadow: 8px 8px 16px rgba(30, 50, 80, 0.2), -8px -8px 16px rgba(255, 255, 255, 0.7);
}

.widget ul {
  list-style-type: none;
  padding: 0;
}

.widget ul li {
  font-size: 0.8rem;
  cursor: pointer;
  display: block;
  margin-bottom: 0.5rem;
  padding: 0.5rem;
  border-radius: 8px;
  text-align: left;
  transition: all 0.2s ease-in-out;
  background: transparent;
  color: #2e3440;

  @media (max-width: 1024px) {
    margin-bottom: 0;
  }
}

.widget ul li.active {
  color: #ffffff;
  background: #6359e9;
}

.widget ul li:hover {
  background: #e0e6ef;
  color: #6359e9;
}
